* {
    box-sizing: border-box;
}

body {
    font-family: $font-open-sans;
}
  
a {
    text-decoration: none;
}

h2 {
    font-family: $font-raleway;
    font-size: 2.25rem;
    text-align: center;
}

.container {
    width: 1352px;
    margin: 0 auto;

    @include notebook{
        width: 100%;
        padding: 0 15px;
    }
}

.logo {
    font-family: $font-raleway;
    font-size: 2rem;
    font-weight: 300;
    color: $color-primary;
}