.btn {
    color: white;
    font-weight: bold;
    display: inline-block;
    padding: 16px 65px;
    border-radius: 50px;
    border: none;
  
  
    &-primary {
      background-color: $color-primary;
      border-radius: 50px;
      box-shadow: 3px 10px 14px $color-primary-shadow;
    }
  
    &-redish {
      background-color: $color-redish;
    }
  
    &-green {
      background-color: $color-green;
    }
  
    &-large {
      padding: 22px 155px;
    }
  }
  