.header{
    height: 70px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, .16);

    &-container{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        padding-left: 25px;
        padding-right: 25px;
    }

    .nav{
        &-element{
            padding: 0 26px;

            &:last-child{
                padding-right: 0;
            }
        }

        &-link{
            color: $color-grey;
            font-weight: 700;
            font-size: 0.6875rem;
            text-transform: uppercase;
            padding: 28px 18px 22px;
            border-bottom: 4px solid transparent;
            letter-spacing: 0.1875rem;

            &:hover{
                color: $color-black;
                border-color: $color-primary ;
            }
        }
    }

    &-nav{
        display: flex;

        @include mobile{
            position: absolute;
            left: 0;
            top: 95px;
            flex-direction: column;
            background-color: white;
            width: 100%;
            padding-bottom: 20px;
            display: none;
            

            .nav-element{
                padding: 0;
            }

            .nav-link{
                display: block;
                padding: 14px 20px;
            }
        }
    }

    .menu{
        &-btn{
            display: none;
            position: absolute;
            top: -9999px;
            left: -9999px;
            opacity: 0;

            @include mobile{
                &:checked{
                    ~ .header-nav{
                        display: flex;
                    }

                    ~ .menu-toggle{
                        span{
                            &:nth-child(1){
                                transform: rotate(45deg) translate(2px, -5px);
                            }
                            &:nth-child(2){
                                opacity: 0;
                            }
                            &:nth-child(3){
                                transform: rotate(-45deg) translate(11px, -5px);
                            }
                        }
                    }
                }
            }
        }

        &-toggle{
            display: none;

            span{
                width: 20px;
                height: 2px;
                display: block;
                margin-bottom: 3px;
                background-color: $color-primary;
                transition: .3s all ease-in-out;

                &:last-child{
                    margin-bottom: 0;
                }
            }

            @include mobile{
                display: block;
            }
        }
    }
}











